@import '../../theme/toRem';

.home {

  h2 {
    margin-bottom: toRem(20);
    font-size: toRem(20);
    font-weight: bold;
  }

  article {
    display: grid;
    gap: toRem(28);

    p {
      grid-column: 1 / 3;
      grid-row: 1 / 3;
      font-size: toRem(16);
      line-height: toRem(24);

      a {
        color: white;
        font-size: inherit;
        font-weight: bold;
        text-decoration: none;
      }
    }

    img {
      width: 241px;
      height: 181px;
    }

    img:first-child {
      grid-column: 3 / 5;
      grid-row: 1 / 2;
    }

    img:nth-child(2) {
      grid-column: 5 / 7;
      grid-row: 1 / 3;
    }
  }
}
